body{
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background-color: #2f2f2f;
    background-color: #eff3f7;
    height: 100%;
}

html, body{height: 100%}

.loader{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.movies{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 50px;
    width: 80%;
    align-items: flex-start;
    padding-top: 70px;
}
.movies .movie{
    width:45%;
    background-color: white;
    margin-bottom: 70px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    font-weight: 300;
    padding: 20px;
    position: relative;
    color: #b2b3bc;
}
.movie img{
    position: relative;
    top: -50px;
    max-width: 120px;
    margin-right: 30px;

}
.movie .movie__title,
.movie .movie__year {
    margin: 0;
    font-weight:  300;
    color: #b2b3bc;
}

.movie .movie__title{
    margin-bottom: 5px;
    font-size: 24px;
}
.movie .movie__year,
.movie .movie__genres {
    color: #b2b3bc;
}